import { defineComponent } from "vue";
import Sidebar from "@/components/Sidebar.vue";
import CoinIcon from "@/components/CoinIcon.vue";
export default defineComponent({
    name: "App",
    components: { CoinIcon, Sidebar },
    computed: {
        benchmark() {
            const ethereum = this.$store.state.selectedCoins.find((coin) => {
                return coin.id === "ethereum";
            });
            return ethereum.percentage();
        },
    },
    methods: {
        getColor(coin) {
            if (coin.id === "ethereum")
                return " table-primary";
            if (!this.benchmark)
                return "";
            const percentage = coin.percentage();
            console.log(coin.id, percentage, this.benchmark);
            if (!percentage)
                return " table-secondary";
            const winnerCutoff = this.benchmark >= 0 ? this.benchmark * 1.5 : this.benchmark * 0.5;
            if (percentage >= winnerCutoff)
                return " table-success";
            const loserCutoff = this.benchmark >= 0 ? this.benchmark * 0.5 : this.benchmark * 1.5;
            if (percentage < loserCutoff)
                return " table-danger";
            return "";
        },
    },
});
